'use strict'
import React from 'react';
import common from './lib/common';
import { render } from 'react-dom';
import { Router, Route, IndexRoute, hashHistory, browserHistory } from 'react-router';
import { Provider } from 'react-redux'

//index 
import Home from './index/home';
//

// import AppointmentHome from './appointment/home';
//login 
import LoginHome from './login/home';
import Register from './login/register';
import ForgotPass from './login/forgotpass';
//
import ServiceHome from './service/home';
//users
import UsersHome from './users/home';

import NoMatch from './no_match';
import Main from './main';
//  identity
import Identity from './users/identity'
import MyOrder from './users/myorder'
import Collect from './users/collect'
import Message from './users/message'
import Coupons from './users/coupons'
import WalletInvoice from './users/wallet_invoice'
import Invoice from './users/invoice'
import InvoiceList from './users/invoice_list'
import Evaluate from './users/evaluate'
import DaiJinQuan from './users/daijinquan'
//
import ShoppingHome from './shopping/home'
import ShoppingDetail from './shopping/detail'
//
import payHome from './pay/home'
import payOk from './pay/payOk'
import payMode from './pay/pay_mode'
import PayWeChat from './pay/wechat'
//
import Xieyi from './xieyi/home'
//
import ShoppingMall from './appointment/ShoppingMall'
// 
import Maps from './map/home'
// 
import Share from './share/share'
//
import DiscountList from './pay/discount_list'

import SetHome from './set/home'
//css
import '../../Content/main.scss';
import '../../Content/lsh.scss';
import '../../Content/zy.scss';
import '../../Content/cjl.scss';
import '../../Content/hhl.scss';
// import 'antd/dist/antd.css';
const store = createStore(common.reducer, 0);
import { createStore } from 'redux';
class RouterApp extends React.Component {
    render() {
        return (
            <Router history={hashHistory}>
              <Route path="/" component={Main}>
                    <IndexRoute component={Home} onEnter={() => { sessionStorage.index = 1 }} />
                    {/*<Route path="/appointment/home" component={AppointmentHome} onEnter={() => { sessionStorage.index = 2 }} />*/}
                    <Route path="/service/home" component={ServiceHome} onEnter={() => { sessionStorage.index = 3 }} />
                     <Route path="/appointment/ShoppingMall" component={ShoppingMall} onEnter={() => { sessionStorage.index = 4 }} />{/**/}
                    {/* <Route path="/users/home" component={UsersHome} onEnter={() => { sessionStorage.index = 5 }} /> */}
             </Route>
            
             <Route path="/login/home" component={LoginHome} />{/**/}
             <Route path="/login/register" component={Register} />{/**/}
             <Route path="/login/forgotpass" component={ForgotPass} />{/**/}
             <Route path="/users/identity" component={Identity} />{/**/}
             <Route path="/users/myorder(/:status)" component={MyOrder} onEnter={() => { sessionStorage.index = 2 }} />{/**/}
             <Route path="/users/collect" component={Collect} />{/**/}
             <Route path="/users/message" component={Message} />{/**/}
             <Route path="/users/coupons" component={Coupons} />{/**/}
             <Route path="/users/wallet_invoice" component={WalletInvoice} />{/**/}
             <Route path="/users/invoice" component={Invoice} />{/* */}
             <Route path="/users/invoice_list" component={InvoiceList} />{/* */}
             <Route path="/users/evaluate(/:orderID)" component={Evaluate} />{/* */}
             <Route path="/set/home" component={SetHome} />{/**/}
             <Route path="/pay/discount_list" component={DiscountList} />{/**/}
             <Route path="/shopping/home" component={ShoppingHome} />{/**/}
             <Route path="/pay/home(/:sourceType)" component={payHome} />{/**/}
             <Route path="/pay/payok" component={payOk} />{/**/}
             <Route path="/pay/pay_mode" component={payMode} />{/**/}
             <Route path="/xieyi/home(/:num)" component={Xieyi} />{/**/}
             <Route path="/map/home" component={Maps} />{/**/}
             <Route path="/share/share(/:opencode)" component={Share} />{/**/}
             <Route path="/users/daijinquan" component={DaiJinQuan} />{/**/}
            
             <Route path="/pay/wechat/:sourceType/:sourceIds" component={PayWeChat} />{/**/}
             <Route path="/shopping/detail(/:id)(/:is_renew)" component={ShoppingDetail} />{/**/}
              <Route path="*" component={NoMatch}></Route>
             {/* <Route path="/product/teahousedetail" component={ProductTeaHouseDetail} /> */}
             {/* <Route path="/product/teahouselist" component={ProductTeaHouseList} /> */}






            </Router>
        )
    }
}

render((
    <Provider store={store}>
        <RouterApp />
    </Provider>
), document.getElementById('baseApp'));


